import { registerBlockType } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';
import { useBlockProps, InspectorControls, MediaUpload } from '@wordpress/block-editor';
import { PanelBody, TextControl, Button } from '@wordpress/components';

registerBlockType('sequoia/hero', {
  title: __('Hero', 'sequoia'),
  icon: 'format-image',
  category: 'sequoia-home',
  attributes: {
    title: { type: 'string', default: 'Título de ejemplo' },
    text: { type: 'string', default: '' },
    imageUrl: { type: 'string', default: '' }
  },
  edit: ({ attributes, setAttributes }) => {
    const { title, text, imageUrl } = attributes;
    const blockProps = useBlockProps();

    return (
      <div {...blockProps}>
        <InspectorControls>
          <PanelBody title="Contenido del Hero que tal">
            <TextControl
              label="Título"
              value={title}
              onChange={(val) => setAttributes({ title: val })}
            />
            <TextControl
              label="Texto"
              value={text}
              onChange={(val) => setAttributes({ text: val })}
            />
            <MediaUpload
              onSelect={(media) => setAttributes({ imageUrl: media.url })}
              type="image"
              render={({ open }) => (
                <Button onClick={open} isPrimary>
                  {imageUrl ? 'Cambiar imagen' : 'Subir imagen'}
                </Button>
              )}
            />
          </PanelBody>
        </InspectorControls>

        <h1>{title}</h1>
        <p>{text}</p>
        {imageUrl && <img src={imageUrl} alt="" />}
      </div>
    );
  },
  save: () => {
    return null; // Usamos render_callback en PHP
  }
});
